<template>
	<view class="page">
		<ch-right-header :title="$t('工单监视')" path="/pages/order/search"></ch-right-header>
		<view class="content bgwhite" style="padding-top:44px">
			<view v-if="!isGet">
				<mescroll-uni v-if="list.length > 0" ref="mescrollRef" @init="mescrollInit" :topbar="false"
					:fixed="false" @down="downCallback" @up="upCallback" :down="downOption" :up="upOption">
					<view class="order-wrap bgwhite">
						<view class="order-list ch-flex" v-for="(item, index) in list" :key="index"
							@click="$nav.to('/pages/order/detail?id=' + item.mo)">
							<view class="ch-flex-1">
								<view class="ch-flex">
									<view class="label nowwidth">{{ $t('工单号') }}：</view>
									<view class="ch-flex-1 value">{{ item.mo }}</view>
								</view>
								<view class="ch-flex">
									<view class="label nowwidth">{{ $t('产品编号') }}：</view>
									<view class="ch-flex-1 value">{{ item.itemNo }}</view>
								</view>
								<view class="ch-flex">
									<view class="label nowwidth">{{ $t('产品描述') }}：</view>
									<view class="ch-flex-1 value"
										style="width: auto; height: 20px; overflow: hidden; display: inline-block;">{{
											item.itemName }}</view>
								</view>
								<view class="ch-flex">
									<view class="label nowwidth">{{ $t('欠生产数') }}：</view>
									<view class="ch-flex-1 value">{{ item.remainNumber }}</view>
								</view>
								<view class="ch-flex">
									<view class="label nowwidth">{{ $t('完成率') }}：</view>
									<view class="ch-flex-1 value">
										<view style="width: 300upx;height: auto;display: flex;flex-direction: row;">
											<u-line-progress :percentage="completionRate(item)" height="auto">
												<view class="u-percentage-slot" activeColor="#21C9A0"></view>
											</u-line-progress>
											<text class="u-percentage-slot" style="padding-left:10upx"> {{ completionRate(item) }}%</text>
										</view>
									</view>
								</view>
								<view class="ch-flex">
									<view class="label nowwidth">{{ $t('计划结束时间') }}：</view>
									<view class="ch-flex-1 value">{{ item.planFinishTime }}</view>
								</view>
							</view>
							<view class="rightWrap">
								<u-icon name="arrow-right" size="18" color="#bfbfbf"></u-icon>
							</view>
						</view>
					</view>
				</mescroll-uni>
				<view class="emptyView" v-else>
					<u-empty :text="$t('数据为空')" mode="list"></u-empty>
				</view>
			</view>
			<template v-else>
				<u-loading-icon text="loading"></u-loading-icon>
			</template>
		</view>
	</view>
</template>

<script>
import listScroll from "@/mixins/listScroll";
import Api from "@/api/order/index.js"
export default {
	mixins: [listScroll],

	data() {
		return {
			apiUrl: Api.list,
			method: 'get',
			query: {},
			queryParams: {
				orderNo: null,
				machineNo: null,
				mouldNo: null,
				itemNo: null,
				dispatchStatus: null,
				planStartTime: null,
				planEndTime: null,
				actualStartTime: null,
				actualEndTime: null
			},
		};
	},

	onLoad() {
		//搜索功能
		uni.$on('returnOrderData', data => {
			console.log('details发出的值===>', data);
			if (data) {
				this.isGet = true
				this.refreshUI(data);
			}
		});
	},
	onUnload() {
		uni.$off('returnOrderData')
	},
	methods: {
		//请求前组合参数
		beforeGetList() {
			this.list = []
		},

		//完成率
		completionRate(item) {
			const rate = item.finishRate != null ? item.finishRate : 0.0
			return `${rate.toFixed(2)}`
		},
		//欠生产数
		remainder(item) {
			const num = item.dispatchQty - item.prodQty
			return num > 0 ? num : 0
		},

		//刷新界面
		refreshUI(data) {
			this.query = data
			this.pager.pageNum = 1 //如果是搜索从第一页开始
			this.getList();
		}
	}
}
</script>

<style lang="scss" scoped></style>
